h1 {
  text-align: center;
  font-size: 3.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 1rem;
  text-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.cards__subtitle {
  text-align: center;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 4rem;
  font-weight: 300;
  letter-spacing: 1px;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.cards__wrapper {
  position: relative;
  margin: 0 0 3rem;
}

/* Section styling */
.cards__featured-section,
.cards__other-section {
  margin-bottom: 4rem;
}

/* Add visual separation between sections */
.cards__other-section::before {
  content: '';
  display: block;
  width: 60px;
  height: 3px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
  margin: 0 auto 3rem auto;
  border-radius: 2px;
}

/* Featured projects styling */
.cards__featured {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
  max-width: 900px;
  margin: 0 auto 3rem auto;
}

.cards__featured .cards__item {
  transform: scale(1.05);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.cards__featured .cards__item:hover {
  transform: scale(1.08);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.5);
}

.cards__items {
  margin-bottom: 3rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  justify-items: center;
  width: 100%;
}

.cards__item {
  display: flex;
  flex: 1;
  width: 100%;
  max-width: 380px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.2);
  position: relative;
}

/* Star styling */
.cards__item__star {
  position: absolute;
  top: 15px;
  left: 15px;
  z-index: 10;
  cursor: pointer;
  padding: 5px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.cards__item__star:hover {
  background: rgba(0, 0, 0, 0.8);
  transform: scale(1.1);
}

.star {
  font-size: 18px;
  display: block;
  transition: all 0.3s ease;
  line-height: 1;
}

.star--filled {
  color: #ffd700;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.8);
  animation: starGlow 2s ease-in-out infinite alternate;
}

.star--empty {
  color: rgba(255, 255, 255, 0.6);
}

.star--empty:hover {
  color: rgba(255, 215, 0, 0.8);
}

@keyframes starGlow {
  0% {
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.8);
  }
  100% {
    text-shadow: 0 0 20px rgba(255, 215, 0, 1), 0 0 30px rgba(255, 215, 0, 0.5);
  }
}


.cards__item:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
  border-color: rgba(255, 255, 255, 0.4);
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  text-decoration: none;
  color: inherit;
  height: 100%;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 55%;
  overflow: hidden;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  top: 12px;
  right: 12px;
  padding: 8px 12px;
  font-size: 11px;
  font-weight: 600;
  color: #fff;
  background: linear-gradient(135deg, rgba(22, 34, 42, 0.9), rgba(58, 96, 115, 0.9));
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  filter: brightness(0.9) contrast(1.1);
}

.cards__item:hover .cards__item__img {
  transform: scale(1.05);
  filter: brightness(1) contrast(1.2);
}

.cards__item__info {
  padding: 1.8rem 2rem 2rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95), rgba(248, 250, 252, 0.95));
}

.cards__item__header {
  font-size: 1.4rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 0.8rem;
  line-height: 1.3;
  letter-spacing: -0.02em;
}

.cards__item__text {
  color: #4a5568;
  font-size: 0.95rem;
  line-height: 1.6;
  margin-bottom: 1rem;
  flex-grow: 1;
  font-weight: 400;
}

.cards__read-more {
  color: #3182ce;
  font-weight: 600;
  font-size: 0.9rem;
  text-decoration: none;
  align-self: flex-start;
  transition: all 0.3s ease;
  border-bottom: 2px solid transparent;
}

.cards__read-more:hover {
  color: #2c5aa0;
  border-bottom-color: #3182ce;
}

/* Responsive Design */
@media only screen and (min-width: 1200px) {
  .cards__items {
    grid-template-columns: repeat(3, 1fr);
    max-width: 1200px;
    margin: 0 auto 3rem auto;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1199px) {
  .cards__items {
    grid-template-columns: repeat(2, 1fr);
    max-width: 800px;
    margin: 0 auto 3rem auto;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__items {
    gap: 1.5rem;
  }
  
  .cards__item {
    max-width: 100%;
  }

  .cards__featured .cards__item {
    transform: scale(1.02);
  }

  .cards__featured .cards__item:hover {
    transform: scale(1.05);
  }

  h1 {
    font-size: 2.8rem;
  }
}

@media only screen and (max-width: 768px) {
  .cards {
    padding: 3rem 1.5rem;
  }

  .cards__container {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .cards__wrapper {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
  }

  .cards__items {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    width: 100%;
    margin: 0 0 2.5rem 0;
    padding: 0;
  }

  .cards__featured {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    width: 100%;
  }

  .cards__featured-section {
    width: 100%;
  }

  .cards__other-section {
    width: 100%;
  }

  .cards__featured .cards__item {
    transform: scale(1);
  }

  .cards__featured .cards__item:hover {
    transform: translateY(-4px);
  }

  h1 {
    font-size: 2.2rem;
  }

  .cards__subtitle {
    font-size: 1rem;
    margin-bottom: 2.5rem;
  }

  .cards__item__info {
    padding: 1.5rem 1.5rem 1.8rem;
  }

  .cards__item__header {
    font-size: 1.2rem;
  }

  .cards__item__text {
    font-size: 0.9rem;
  }
}

@media only screen and (max-width: 480px) {
  .cards {
    padding: 2rem 1rem;
  }

  h1 {
    font-size: 1.8rem;
  }

  .cards__wrapper {
    max-width: 340px;
  }

  .cards__items {
    gap: 1.2rem;
    width: 100%;
  }

  .cards__item__info {
    padding: 1.2rem 1.2rem 1.5rem;
  }
}